import {defaults,modal} from './modal'

export default (_text, title, callbackOk, callbackCancel, textOk, textCancel) => {
    // 表达式声明
    if (typeof title === 'function') {
        if (textOk){
            textCancel = textOk
        }
        if (callbackCancel){
            textOk = callbackCancel
        }
        if (callbackOk){
            callbackCancel = callbackOk
        }
        callbackOk = title
        title = undefined;
    }
    // 函数对象式声明
    else if (typeof _text === 'object') {
        let { text = '', title = defaults.modalTitle, onCancelClick, onOkClick, okText = defaults.modalButtonOk, cancelText = defaults.modalButtonCancel, } = _text

        return modal({
            text,
            title,
            buttons: [
                {text: cancelText, onClick: onCancelClick},
                {text: okText, bold: true, onClick: onOkClick}
            ],
            extraClass: 'remove-on-close',
        });
    }
    return modal({
        text: _text || '',
        title: typeof title === 'undefined' ? defaults.modalTitle : title,
        buttons: [
            {text: textCancel || defaults.modalButtonCancel, onClick: callbackCancel},
            {text: textOk || defaults.modalButtonOk, bold: true, onClick: callbackOk}
        ],
        extraClass: 'remove-on-close',
    });
};


